import React, { Component } from 'react';
import '../styles/case-studies.css'

const title = "Operationalize Intelligence";

const subheading1 = "Business Challenges";

const text1 = "Organizations are increasingly adopting predictive analytics. Many are now using dozens or even hundreds of predictive analytic models. These models are now used in real-time decision-making and in operational, production systems. However, many analytic teams rely on tools and approaches that will not scale to this level of adoption. These teams lack a repeatable, efficient process for creating and effectively deploying predictive analytic models into production. To succeed they need to operationalize analytics.";

const text2 = "Operationalizing analytics requires both a repeatable, enterprise-scale process for developing the dozens or hundreds of predictive analytic models that will be needed and a reliable architecture for deploying predictive analytic models into production systems.";

const text3 = "The first step in operationalizing analytics is moving from a silo-based data science functional group to an organization process for building analytic models. This means moving away from individual scripting environments where every task is performed by hand, reuse is limited, only small numbers of expert analytic practitioners are involved, and these practitioners do not follow a standard process. Such an approach can and does produce high-quality models, but it cannot scale to allow an organization to become broadly analytic in its decision-making. To enable an enterprise scale AI firm, access to data must be standardized based on well-defined metadata and standard sets of data about customers, products and other business entities. Definitions of this data are shared, and analytical datasets are generated in a repeatable and increasingly automated way. This more systematic approach to data management feeds into a workbench environment for defining the modelling workflow.";

const text4 = "The cloud revolution in the data modernization practices such as AWS, MS Azure provides flexibility to develop standard data pipelines and shared through a repository which in turn enables streamlining and standardizing of how modelling is performed. These pipelines can use advanced processing frameworks such as Spark, Scala to push data preparation, transformation and even algorithms into the data infrastructure, improving throughput by reducing data movement. ";

const subheading2 = "Benefits"

const text5 = "These modern data ecosystems support the ongoing management and monitoring of models once they are in use by allowing analytics teams to set up automated monitoring of models to see when they need to be retrained or even completely rebuilt. These capabilities also help track the performance of models to confirm their predictive power and behaviour. These pipelines can often be wrapped with an interface suitable for less technical users. Such features allow less technical users to build and execute workflows that take advantage of the automation capabilities to produce large numbers of good-enough models quickly. Working with an analytic team, these users can produce first-cut models and participate more fully in reviews of models while allowing the analytic team to focus on high-value, high complexity problems.";

const text6 = "Operationalizing analytics requires this, but it also requires a more systematic focus on the use of analytics in operational systems. As organizations expand their use of predictive analytics, it becomes increasingly clear that the long-term value of predictive analytics comes in improving the quality and effectiveness of operational decisions. Operational decisions are highly repeatable decisions about a single customer or transaction. Because they are made repeatedly, these decisions create the data needed for effective predictive analytics. Many analytic teams think of themselves as done when the model is built and validated. A team focused on operationalizing analytics will instead focus on the point at which operational decisions are being made more effectively thanks to analytics embedded in the systems used to make those decisions. This requires a change in focus and the use of some more modern technologies. One way to make predictive analytic models available in operations is to use in-database scoring infrastructure, which takes models and pushes them directly into the core of an organization’s operational data stores.";

const text7 = "Many applications for fraud detection credit risk management or customer intelligence have been designed to allow new models to be rapidly integrated into operational systems. As more packaged applications tackle decision-making, this capability will only become more widespread, giving analytic teams more deployment options and further reducing the barriers to using predictive analytics in operations.";

export default class CaseStudies2 extends Component {


  render() {
    return (
      <div className="card mb-4">
                                    
               <div className="card-title padding-20">
                  <h3 className="blog-head">{title}</h3>
               </div>
               
               <img alt="" className="img-fluid" src={require('../images/cases/operationalize-intelligence.jpg')}/>
               
               <div className="card-body row">
                  <h5>{subheading1}</h5>
                  <p>{text1}</p>
                  <p>{text2}</p>
                  <p>{text3}</p>
                  <p>{text4}</p>
                  <h5>{subheading2}</h5>
                  <p>{text5}</p>
                  <p>{text6}</p>
                  <p>{text7}</p>
               </div>

            </div>
    );
  }
}

