import React, { Component } from 'react';
import '../styles/case-studies.css'

const title = "Leveraging AI to Efficiently assist Sales";


const subheading1 = "Business Challenges";
const subheading2 = "Benefits";
const subheading3 = "Collaboration";

const text1 = "There is a growing interest in applying machine learning techniques on sales and marketing data. Extracting meaningful insights from sales and marketing data has been prominent as each company wants to be a pioneer in improving the sales numbers. It is a daunting task to proactively target the users who are browsing through the website yet to share any information through form fills, subscriptions, etc.";

const text2 = "With constraints on resources and time, the leader sought to automate the pro-active targeting, ensure the data’s compliance with regulatory requirements, and deploy a new platform that could meet the velocity demands of the technology company over time.";

const text3 = "We will be seeing how we have disrupted the sales by enriched data in helping the purchase ease and high-quality sales."

const text4 = "The technology company and ngenux, with support from several SMEs, reimagined the entire sales process to support artificial intelligence (AI) learning. Powered by ngenux’s Automated sales solution, the new pipeline would replace the company’s traditional system of waiting for the user to fill the forms and processing user data. This enabled the technology company to reach out to 1 million customers proactively in a fraction of time when they visit the site with higher accuracy. As a result, the firm has saved many man-hours and eliminated human-input errors in the processing phase itself. ";

const text5 = "The solution had five components: user tracking from a website, third party data, in house mapping algorithm powered by AI, proactive filtering and recommendation engine for customized alerts. This combination enabled the enterprise to use advanced AI to process and classify data with unmatched efficiency while giving human experts the ability to double-check data if it was flagged by the algorithms as at-risk for errors.";

const text6 = "This unmanned sales targeting solution automated nearly 80% of the data collection activities, reducing the required time by 45% compared to previous manual processes. It also allowed taking the decisions powered by AI dashboard which proactively sends the customized alerts to sales for targeting. The impact was a report backed by AI that positioned the salesperson to target the B2B user while improving the data’s reusability by internal and external stakeholders alike.";


export default class CaseStudies5 extends Component {


  render() {
    return (
      <div className="card mb-4">
                                    
                <div className="card-title padding-20">
                    <h3 className="blog-head">{title}</h3>
                </div>
                
                <img alt="" className="card-img-top" src={require('../images/cases/leveraging-ai.jpg')}/>
                
                <div className="card-body row">
                    <h5>{subheading1}</h5>
                    <p>{text1}</p>
                    <p>{text2}</p>
                    <p>{text3}</p>
                    <h5>{subheading3}</h5>
                    <p>{text4}</p>
                    <p>{text5}</p>
                    <img alt="" className="card-img-top" src={require('../images/cases/leveraging-ai-flow.jpg')}/>	
                    <h5>{subheading2}</h5>
                    <p>{text6}</p>
                </div>

            </div>
    );
  }
}

