import React, { Component } from 'react';
import { BrowserRouter as Router, Switch, Route} from 'react-router-dom';
import './App.css';
import Home from './components/Home.jsx';
import WhoWeAre from './components/WhoWeAre.jsx';
import WhatWeDo from './components/WhatWeDo.jsx';
import CaseStudiesDiv from './components/CaseStudiesDiv';
/* import CaseStudies1 from './components/CaseStudies1.jsx';
import CaseStudies2 from './components/CaseStudies2.jsx';
import CaseStudies3 from './components/CaseStudies3.jsx';
import CaseStudies4 from './components/CaseStudies4.jsx';
import CaseStudies5 from './components/CaseStudies5.jsx'; */
/* import WhyUs from './components/WhyUs.jsx'; */
import Contact from './components/Contact.jsx';

import PrimaryNavbar from './components/PrimaryNavbar.jsx';
import FooterNavbar from './components/FooterNavbar.jsx';

import {
  CSSTransition,
  TransitionGroup,
} from 'react-transition-group';

class App extends Component {
  render() {
    return (

      <div id="top">
        
        <Router>
          <div>
            <PrimaryNavbar/>
            <Route render={({location}) => (
              <TransitionGroup>
                <CSSTransition
                  key={location.key}
                  timeout={300}
                  classNames="fade"
                >
                  <Switch location={location}>
                    <Route exact path="/" component={Home}/>
                    <Route path="/who-we-are" component={WhoWeAre}/>
                    <Route path="/what-we-do" component={WhatWeDo}/>
                    <Route path="/case1" render={(props)=><CaseStudiesDiv {...props} caseId="case1"/>}/>
                    <Route path="/case2" render={(props)=><CaseStudiesDiv {...props} caseId="case2"/>}/>
                    <Route path="/case3" render={(props)=><CaseStudiesDiv {...props} caseId="case3"/>}/>
                    <Route path="/case4" render={(props)=><CaseStudiesDiv {...props} caseId="case4"/>}/>
                    <Route path="/case5" render={(props)=><CaseStudiesDiv {...props} caseId="case5"/>}/>
                    {/* <Route path="/case-studies/:case" component={CaseStudies}/> */}
                    {/* <Route path="/why-us" component={WhyUs}/> */}
                    <Route path="/contact" component={Contact}/>
                  </Switch>
                </CSSTransition>
              </TransitionGroup>
            )} />
            <FooterNavbar/>
          </div>
        </Router>
        
      </div>

    );
  }
}

export default App;
