import React, { Component } from 'react';
import '../styles/case-studies.css'

const title = "Beyond the Firewalls";

const text1 = "The volume, variety, and velocity of big data have opened a new world of possibilities for human progress. Finding new cures for cancer, discovering unknown particles, and even preventing crime are just a few of the promises this new world holds. Evidence has abounded that sound statistical practices such as ensuring data quality, designing experiments correctly, establishing a clear methodology to approaching problems, and applying domain-specific knowledge are essential to the success of any AI project and, on a larger scale, to the success of the organization-wide digital transformation.";
const subheading1 = "Business Challenges";
const text2 = "To expand on this a significant value is derived by performing ML and statistical techniques on the data which is present inside the organization in the form of support engagement, contractual transaction, pre-purchase journey established by marketing and sales activities and so on and so forth. The data within the framework helps us to draw patterns for the customers who made a purchase with us in the form of customer segmentation, attrition analysis and success scorecards."
const text3 = "However, a significant form of intelligence is present in the world which is beyond the firewalls of the customer framework. Data in the form the ratings, surveys, employee opinion in the glassdoor, followers of the company page in social media platforms, observers of the C Level executives are the few to begin with. This external data has now become the differentiator in the way analytics can transform the company. Systematic grooming and evolution of the Chief Data office will help us identify the nuances of gathering external data in terms of what data to collect, which vendors to engage, how frequently to refresh and how to eliminate the data redundancy by ensuring that different business teams don’t procure the same data at the different points of time.";
const subheading2 = "Benefits"
const text4 = "Once the data collection strategy is established enterprises need to make sure to run a enablement and awareness program educating the practitioner and citizen data scientists so that they can bake this data and corresponding intelligence into their models to make the models more efficient and add more contextual richness into their Analytics offerings which in turn fosters effective decision making and better RoI."

export default class CaseStudies1 extends Component {


  render() {
    return (
            <div className="card mb-4">
                
                <div className="card-title padding-20">
                    <h3 className="blog-head">{title}</h3>
                </div>
                
                <img alt="" className="img-fluid" src={require('../images/cases/beyond-firewalls.jpg')}/>
                
                <div className="card-body row">
                    <p>{text1}</p>
                    <h5>{subheading1}</h5>
                    <p>{text2}</p>
                    <p>{text3}</p>
                    <h5>{subheading2}</h5>
                    <p>{text4}</p>
                </div>

            </div>
    );
  }
}

