import React, { Component } from 'react';
import '../styles/case-studies.css'

const title = "Centralizing Data Science";


const subheading1 = "Business Challenges";

const text1 = "As enterprises look to develop their AI strategy, several questions emerge: Where do we start? How do we demonstrate ROI?													";
              
const text2 = "The first step business functions within large organization kick off their analytical journey is by setting up data science teams to cater to the use cases confine to their space. For example, the digital marketing team deploys a group of data science team to analyse the clickstream data to gauge the conversions and gather intelligence to increase the top of the funnel. Similarly, a Sales operations group sets up an analytics team to develop prediction models for win loss and opportunity scoring and likewise a customer success department has teams working on predicting potential churn and analysing sentiments of customers. While it is obvious and a known fact that all of the data science teams have to work together, and insights have to be shared across functions for effective decision making for organization to grow to the next level, in a practical world most of the companies fail to do so since all of these teams work in silos. The decentralized mode of operating analytics functions causes other problems as well such "

const list1 = [
                "Operationalization Setup and challenges",
                "Transparency of models",
                "Inconsistent Metrics and SLAs",
                "Skills redundancy",
                "Confusion in Interpretation",
                "Lack of awareness of initiatives",
                "Operationalization Setup and challenges"
              ]

const text3 = "The one stop solution for addressing this challenge is by driving the concept of Centralizing Data Science function within the organization. The adoption barriers that organizations face are managerial and cultural rather than related to data and technology."

const text4 = "The Centralized model allows for business enabling governance model instead of typical technology centric. It helps in centrally manage technology complexity instead of various teams wasting time and energy separately. It reduces the delay in setup so that teams can quickly start to collaborate and finally drives end user adoption by making it easy to engage."

const subheading2 = "Benefits";

const text5 = "The benefits of a centralized Data Science model would help the organization define the shared service organization, standard processes and methodologies and continuous improvement by feedback, best practices and maturity assessment. From a platform standpoint, it facilitates the on-demand provisioning of environment, cost control and define SLAs and measure attainment. With a CXO level backed function, it directly drives faster and better organization adoption by providing Self-service and user empowerment to business stakeholders."

const text6 = "A centralized data science system can constantly measure improvements in key metrics over time and compare projected versus realized KPIs. The system can learn and make improvements based on system recommendations overridden by business users. Intuitive reason codes, when user overrides are made, can help enhance the system’s future recommendations. The system can also provide insights into opportunity costs (the cost of a missed opportunity, if a recommended decision is not taken). These insights can then drive change management and adoption at the executive level. A well architected centralized data science engine can drive reusability, scalability, transparency, and accountability."


export default class CaseStudies4 extends Component {


  render() {
    return (
      <div className="card mb-4">
                                    
                <div className="card-title padding-20">
                    <h3 className="blog-head">{title}</h3>
                </div>
                
                <img alt="" className="card-img-top" src={require('../images/cases/centralizing-data-science.jpg')}/>
                
                <div className="card-body row">
                    <p>{text1}</p>
                    <h5>{subheading1}</h5>
                    <p>{text2}</p>
                    <p>
                        {
                          list1.map((l, i) => {
                            return <li key={i}>{l}</li>
                          })
                        }
                    </p>
                    <p>{text3}</p>
                    <p>{text4}</p>
                    <h5>{subheading2}</h5>
                    <p>{text5}</p>
                    <p>{text6}</p>
                </div>

            </div>
    );
  }
}

