import React, { Component } from 'react';
import '../styles/case-studies.css'

const title = "Analytics Taxonomy of Digital Marketing";


const subheading1 = "Business Challenges";

const text1 = "Marketing analytics are only as valuable as the insights we pull from them and the strategy we build around those insights. Let’s talk about how you’re going to better integrate this wealth of metrics and data into your marketing strategy. At the outset before we embark into the digital marketing analytical journey its important to understand key Marketing Analytics Terms."
              
const text2 = "Before you can put marketing data into context and use it to drive decision-making and strategy, you need to know what you’re dealing with. It’s hard to make sense of changes in your conversion rate if you don’t actually understand what conversion rate measures. First, we’ll dig into the meaning of a few key marketing analytics."

const subheading3 =  "Web Analytics:"
const text3 = " Oftentimes when we say “analytics,” we think of web analytics – the kind of data we find in Google Analytics and other platforms (like those we use for email marketing, for example). These are just a small part of the data that pertains to marketing activities, but they’re important nonetheless."

const subheading4 =  "Traffic:"
const text4 = " Simply put, traffic measures how many times a particular webpage is viewed. It’s a good indicator of how effectively your marketing efforts are driving people to your website."

const subheading5 =  "Engagement:"
const text5 = " Engagement can encompass several data points (including bounce rate, time on page, pages per session, etc.), but it’s generally a measure of how and how often people interact with your web content."

const subheading6 =  "Open Rate:"
const text6 = " Of the people who received your email, how many actually clicked and opened the email? This is a good measure of the effectiveness of your email subject lines, as well as your subscribers’s engagement with your brand."

const subheading7 =  "Click-through Rate (CTR):"
const text7 = " When you run a digital ad or include a call-to-action (CTA) on a webpage or email, CTR represents the percentage of those who clicked on your CTA versus all those who saw it."

const subheading8 = "Leads Generated: ";
const text8 = "At the end of the day, the core purpose of marketing is to drive leads – people within your target market who may be interested in what you have to offer. Knowing how many leads you’re getting enables you to understand the effectiveness of your marketing at actually growing sales opportunities.";

const subheading9 = "Conversion Rate: ";
const text9 = "Conversion rate is broadly defined as the percentage of users who take a desired action. More specifically in the world of online marketing, it measures the percentage of website visitors who buy something on your site. Conversion rate can help you optimize things like your web design, copy, CTAs, and other elements to make the most of the traffic you drive there.";

const text10 = "The value of marketing analytics lies, not in measuring them, but in contextualizing them. Translating the numbers from just that – numbers – into insights, learnings, and next steps to improve your marketing.";

const text11 = "The first step to contextualizing data is to understand what each of these metrics truly means. It’s one thing to know that conversion rate = total conversions / total clicks. It’s another thing entirely to understand the real-life side of analytics. What does it actually mean to have a 4.6% conversion rate? Well, it means that, for every 100 clicks on your ad, between 4 and 5 of those people became a customer. It means that 4.6% found your product, web design, and copy compelling enough to trust you to solve a problem for them. When you put data into a broader context, a story begins to emerge. Look at how your conversion rate has changed over the last week, month, and year. What are the marketing actions that correspond with those peaks and valleys? Identifying how specific marketing tactics and campaigns have affected key metrics allows you to translate data into a performance evaluation of sorts – did last month’s email marketing campaign have a positive impact on sales growth?";

const subheading2 = "Benefits";

const text12 = "Having a clear picture of how different tactics affect conversions, sales growth, and other key performance indicators (KPIs) empowers you to make decisions about how to proceed. If you can connect the dots to determine that email marketing boosted sales growth from 3% to 7% month over month, it’s easy to see that the campaign was a success. That means you can make intelligent decisions about where to invest your time and budget going forward to enable the best returns. Another way to pull insights out of data is to go farther in the direction your analytics point you. You know that the email campaign improved sales – think of this datapoint as the X marking the spot, and dig here. Talk to your subscribers. Test different variations of copy, email design, send time, etc. Find out what aspect of the email made it so effective. Insights like that take it a step further, giving you new learnings to not only improve your current campaigns, but to help build an even better strategy the next time around.";

export default class CaseStudies3 extends Component {


  render() {
    return (
      <div className="card mb-4">
               
               <div className="card-title padding-20">
                  <h3 className="blog-head">{title}</h3>
               </div>
               
               <img alt="" className="img-fluid" src={require('../images/cases/digital-marketing.jpg')}/>
               
               <div className="card-body row">
                  <h5>{subheading1}</h5>
                  <p>{text1}</p>
                  <p>{text2}</p>
                  <p><b>{subheading3}</b>{text3}</p>
                  <p><b>{subheading4}</b>{text4}</p>
                  <p><b>{subheading5}</b>{text5}</p>
                  <p><b>{subheading6}</b>{text6}</p>
                  <p><b>{subheading7}</b>{text7}</p>
                  <p><b>{subheading8}</b>{text8}</p>
                  <p><b>{subheading9}</b>{text9}</p>
                  <p>{text10}</p>
                  <p>{text11}</p>
                  <h5>{subheading2}</h5>
                  <p>{text12}</p>
               </div>

            </div>
    );
  }
}

