import React, { Component } from 'react';
import ScrollUpButton from "react-scroll-up-button";

import '../styles/case-studies.css'
import CaseStudies1 from './CaseStudies1';
import CaseStudies2 from './CaseStudies2.jsx';
import CaseStudies3 from './CaseStudies3.jsx';
import CaseStudies4 from './CaseStudies4.jsx';
import CaseStudies5 from './CaseStudies5.jsx';

const components = {
   case1: CaseStudies1,
   case2: CaseStudies2,
   case3: CaseStudies3,
   case4: CaseStudies4,
   case5: CaseStudies5
};

export default class CaseStudiesDiv extends Component {
   Case = (props) => {
      const SpecificCase = components[this.props.caseId];
      return <SpecificCase/>;
  } 
  render() {
   
    return (
      <div className="cover-section-3 jumbotron jumbotron-fluid">
         <div className="row">
            <div className="col-xs-12">          
               
                  <div className="card-section blog-section-margin">
                        <div className="row ">
                            <div className="col-md-12">
                                

                                    {this.Case()}

                                
                            </div>
                        </div>
                        
                        <ScrollUpButton />

                        
                  </div>

            </div>
         </div>
      </div>
    );
  }
}

